.Container4 {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  overflow: hidden;
  color: #fff;

  background: 
    -webkit-gradient(
      linear, 
      left top, left bottom, 
      from(rgba(119, 119, 119, 0.6)), 
      to(rgba(119, 119, 119, 0.6)) 
    ),
    url('./../../../../images/roadtrip.jpg');

  background: 
    -webkit-linear-gradient(
      rgba(119, 119, 119, 0.6), 
      rgba(119, 119, 119, 0.6) 
    ),
    url('./../../../../images/roadtrip.jpg');

  background: 
    -o-linear-gradient(
      rgba(119, 119, 119, 0.6), 
      rgba(119, 119, 119, 0.6) 
    ),
    url('./../../../../images/roadtrip.jpg');

  background: 
    linear-gradient(
      rgba(119, 119, 119, 0.6), 
      rgba(119, 119, 119, 0.6) 
    ),
    url('./../../../../images/roadtrip.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  height: 75vh;
  background-position: 50% 85%;
}

.title .container4_title {
  font-size: 7vh;
  padding: 10px;
  color: #ffffff;
}

.container4_content {
  color: #ffffff;
  font-size: 3vh;
}



